"use client";

import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getDepartments } from "@/lib/api/departmentApi";
import { getRoles } from "@/lib/api/roleApi";
import { getTeams } from "@/lib/api/teamApi";
import { Team } from "@/types/team";
import type { User, UserFilters, UserSort } from "@/types/user";
import {
  bloodGroupOptions,
  gradingOptions,
  probationPeriodOptions,
} from "@/types/user";
import { Filter, Search } from "lucide-react";
import { useMemo, useState } from "react";
import UserTable from "./user-table";

const ITEMS_PER_PAGE = 10;

interface UserTableContainerProps {
  users: User[];
  handleEditUser: (user: User) => void;
  handleDeleteUser: (userId: string) => void;
  handleViewDetails: (user: User) => void;
}

const UserTableContainer = ({
  users,
  handleEditUser,
  handleDeleteUser,
  handleViewDetails,
}: UserTableContainerProps) => {
  const { data: teams } = useSkillQuery<Team[]>({
    queryKey: ["teams"],
    queryFn: getTeams,
  });
  const { data: departments } = useSkillQuery<Team[]>({
    queryKey: ["departments"],
    queryFn: getDepartments,
  });
  const { data: roles } = useSkillQuery<Team[]>({
    queryKey: ["roles"],
    queryFn: getRoles,
  });

  const [currentPage, setCurrentPage] = useState(1);
  const [filters, setFilters] = useState<UserFilters>({
    search: "",
    department: "",
    role: "",
    teamId: "",
    bloodGroup: "",
    grading: "",
    probationPeriod: "",
  });
  const [sort, setSort] = useState<UserSort>({
    field: "fullName",
    direction: "asc",
  });

  // Filter and sort users
  const filteredAndSortedUsers = useMemo(() => {
    const filtered = users.filter((user) => {
      const matchesSearch =
        !filters.search ||
        user.fullName.toLowerCase().includes(filters.search.toLowerCase()) ||
        user.email.toLowerCase().includes(filters.search.toLowerCase()) ||
        user.employeeId.toLowerCase().includes(filters.search.toLowerCase()) ||
        user.mobileNumber.includes(filters.search);

      const matchesDepartment =
        !filters.department || user.departmentId === filters.department;
      const matchesRole = !filters.role || user.roleId === filters.role;
      const matchesTeam = !filters.teamId || user.teamId === filters.teamId;
      const matchesBloodGroup =
        !filters.bloodGroup || user.bloodGroup === filters.bloodGroup;
      const matchesGrading =
        !filters.grading || user.grading === filters.grading;
      const matchesProbation =
        !filters.probationPeriod ||
        user.probationPeriod === filters.probationPeriod;

      return (
        matchesSearch &&
        matchesDepartment &&
        matchesRole &&
        matchesTeam &&
        matchesBloodGroup &&
        matchesGrading &&
        matchesProbation
      );
    });

    // Sort users
    filtered.sort((a, b) => {
      const aValue = a[sort.field];
      const bValue = b[sort.field];

      if (typeof aValue === "string" && typeof bValue === "string") {
        return sort.direction === "asc"
          ? aValue.localeCompare(bValue)
          : bValue.localeCompare(aValue);
      }

      if (typeof aValue === "number" && typeof bValue === "number") {
        return sort.direction === "asc" ? aValue - bValue : bValue - aValue;
      }

      return 0;
    });

    return filtered;
  }, [users, filters, sort]);

  // Pagination
  const totalPages = Math.ceil(filteredAndSortedUsers.length / ITEMS_PER_PAGE);
  const paginatedUsers = filteredAndSortedUsers.slice(
    (currentPage - 1) * ITEMS_PER_PAGE,
    currentPage * ITEMS_PER_PAGE
  );

  const handleFilterChange = (key: keyof UserFilters, value: string) => {
    if (value === "all") {
      setFilters((prev) => ({ ...prev, [key]: "" }));
      setCurrentPage(1);
      return;
    }

    setFilters((prev) => ({ ...prev, [key]: value }));
    setCurrentPage(1);
  };

  const handleSort = (field: keyof User) => {
    setSort((prev) => ({
      field,
      direction:
        prev.field === field && prev.direction === "asc" ? "desc" : "asc",
    }));
  };

  const clearFilters = () => {
    setFilters({
      search: "",
      department: "",
      role: "",
      teamId: "",
      bloodGroup: "",
      grading: "",
      probationPeriod: "",
    });
    setCurrentPage(1);
  };

  return (
    <>
      {/* Filters */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Filter className="h-5 w-5" />
            Filters
          </CardTitle>
          <CardDescription>Filter users by various criteria</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="space-y-2">
              <label className="text-sm font-medium">Search</label>
              <div className="relative">
                <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search users..."
                  value={filters.search}
                  onChange={(e) => handleFilterChange("search", e.target.value)}
                  className="pl-10 w-full"
                />
              </div>
            </div>

            <div className="space-y-2 w-full">
              <label className="text-sm font-medium">Department</label>
              <Select
                value={filters.department}
                onValueChange={(value) =>
                  handleFilterChange("department", value)
                }
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All departments" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All departments</SelectItem>
                  {departments?.data.map((dept) => (
                    <SelectItem key={dept.id} value={dept.id}>
                      {dept.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Role</label>
              <Select
                value={filters.role}
                onValueChange={(value) => handleFilterChange("role", value)}
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All roles" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All roles</SelectItem>
                  {roles?.data.map((role) => (
                    <SelectItem key={role.id} value={role.id}>
                      {role.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Team</label>
              <Select
                value={filters.teamId}
                onValueChange={(value) => handleFilterChange("teamId", value)}
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All teams" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All teams</SelectItem>
                  {teams?.data.map((team) => (
                    <SelectItem key={team.id} value={team.id}>
                      {team.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Blood Group</label>
              <Select
                value={filters.bloodGroup}
                onValueChange={(value) =>
                  handleFilterChange("bloodGroup", value)
                }
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All blood groups" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All blood groups</SelectItem>
                  {bloodGroupOptions.map((bg) => (
                    <SelectItem key={bg} value={bg}>
                      {bg}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Grading</label>
              <Select
                value={filters.grading}
                onValueChange={(value) => handleFilterChange("grading", value)}
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All grades" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All grades</SelectItem>
                  {gradingOptions.map((grade) => (
                    <SelectItem key={grade} value={grade}>
                      {grade}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Probation</label>
              <Select
                value={filters.probationPeriod}
                onValueChange={(value) =>
                  handleFilterChange("probationPeriod", value)
                }
              >
                <SelectTrigger className="w-full">
                  <SelectValue placeholder="All probation status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All probation status</SelectItem>
                  {probationPeriodOptions.map((period) => (
                    <SelectItem key={period} value={period}>
                      {period}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            <div className="flex items-end">
              <Button
                variant="outline"
                onClick={clearFilters}
                className="w-full bg-transparent"
              >
                Clear Filters
              </Button>
            </div>
          </div>

          {/* Active Filters */}
          {Object.entries(filters).some(([, value]) => value) && (
            <div className="flex flex-wrap gap-2 mt-4">
              <span className="text-sm font-medium">Active filters:</span>
              {Object.entries(filters).map(
                ([key, value]) =>
                  value && (
                    <Badge key={key} variant="secondary" className="text-xs">
                      {key.toUpperCase()}
                    </Badge>
                  )
              )}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Users Table */}
      <UserTable
        users={paginatedUsers}
        sort={sort}
        onSort={handleSort}
        onEdit={handleEditUser}
        onDelete={handleDeleteUser}
        onViewDetails={handleViewDetails}
        currentPage={currentPage}
        totalPages={totalPages}
        onPageChange={setCurrentPage}
        totalItems={filteredAndSortedUsers.length}
        itemsPerPage={ITEMS_PER_PAGE}
      />
    </>
  );
};

export default UserTableContainer;
