"use client";

import ErrorPage from "@/components/error-page";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getDepartments } from "@/lib/api/departmentApi";
import {
  deleteUser,
  getUserEmploymentStatus,
  getUsers,
} from "@/lib/api/userApi";
import { getStatusName } from "@/lib/enumHelper";
import { EnumResponse } from "@/types/base";
import { Department } from "@/types/department";
import type { User } from "@/types/user";
import { useMutation } from "@tanstack/react-query";
import { Download, Plus, Users } from "lucide-react";
import { useMemo, useState } from "react";
import { toast } from "sonner";
import UserDetailsModal from "./user-details-modal";
import UserDialog from "./user-dialog";
import UserStatistics from "./user-statistics";
import UserTableContainer from "./user-table-container";

export default function UserManagement() {
  const { data: users, isFetching: isUsersLoading } = useSkillQuery<User[]>({
    queryKey: ["users"],
    queryFn: getUsers,
  });

  const { data: departments, isFetching: isDepartmentsLoading } = useSkillQuery<
    Department[]
  >({
    queryKey: ["departments"],
    queryFn: getDepartments,
  });

  const { data: employmentStatus, isFetching: isEmploymentStatusLoading } =
    useSkillQuery<EnumResponse[]>({
      queryKey: ["employmentStatus"],
      queryFn: getUserEmploymentStatus,
    });

  const { mutateAsync } = useMutation({
    mutationFn: (userId: string) => deleteUser(userId),
    onSuccess: (data) => {
      console.log(data);
    },
  });

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isDetailsModalOpen, setIsDetailsModalOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);

  // Statistics
  const stats = useMemo(() => {
    if (users?.data.length === 0 || departments?.data.length === 0)
      return { total: 0, active: 0, onProbation: 0, departments: 0 };

    const total = users?.data.length ?? 0;

    const activeUsersCount = users?.data.filter(
      (user) =>
        getStatusName(employmentStatus?.data, user.employmentStatus) ===
        "Permanent"
    ).length;

    const onProbationUsers = users?.data.filter(
      (user) =>
        getStatusName(employmentStatus?.data, user.employmentStatus) !=
        "Probation"
    ).length;

    const departmentCount = departments?.data.length ?? 0;

    return {
      total,
      active: activeUsersCount ?? 0,
      onProbation: onProbationUsers ?? 0,
      departments: departmentCount,
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [users, departments, isUsersLoading, isDepartmentsLoading]);

  const handleAddUser = () => {
    setSelectedUser(null);
    setIsDialogOpen(true);
  };

  const handleEditUser = (user: User) => {
    setSelectedUser(user);
    setIsDialogOpen(true);
  };

  const handleViewDetails = (user: User) => {
    setSelectedUser(user);
    setIsDetailsModalOpen(true);
  };

  const handleDeleteUser = async (userId: string) => {
    const toastId = toast.loading("Deleting user...");
    const res = await mutateAsync(userId);
    if (res.success) {
      toast.success("User deleted successfully!", { id: toastId });
      return;
    }
    toast.dismiss(toastId);
  };

  if (
    (!isUsersLoading && !users?.success) ||
    (!isDepartmentsLoading && !departments?.success) ||
    (!isEmploymentStatusLoading && !employmentStatus?.success)
  )
    return (
      <ErrorPage
        message={users?.message ?? "Something went wrong"}
        status={500}
      />
    );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <Users className="h-5 w-5 text-muted-foreground" />
          <Badge variant="secondary" className="text-sm">
            {users?.data.length ?? 0} users
          </Badge>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="sm">
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
          <Button onClick={handleAddUser} size="sm">
            <Plus className="h-4 w-4 mr-2" />
            Add User
          </Button>
        </div>
      </div>

      <UserStatistics stats={stats} />

      <UserTableContainer
        users={users?.data ?? []}
        handleEditUser={handleEditUser}
        handleDeleteUser={handleDeleteUser}
        handleViewDetails={handleViewDetails}
      />

      {/* User Dialog */}
      <UserDialog
        isOpen={isDialogOpen}
        onClose={() => setIsDialogOpen(false)}
        user={selectedUser}
      />

      {/* User Details Modal */}
      <UserDetailsModal
        isOpen={isDetailsModalOpen}
        onClose={() => setIsDetailsModalOpen(false)}
        user={selectedUser}
      />
    </div>
  );
}
