"use client";

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Separator } from "@/components/ui/separator";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getUserEmploymentStatus, getUserReligions } from "@/lib/api/userApi";
import { formatDate, formatSalary } from "@/lib/utils";
import { EnumResponse } from "@/types/base";
import { calculateAge, type User as UserType } from "@/types/user";
import { useQuery } from "@tanstack/react-query";
import {
  AlertTriangle,
  Briefcase,
  Building,
  CakeIcon,
  Calculator,
  Calendar,
  Clock,
  DollarSign,
  Droplets,
  GraduationCap,
  BadgeIcon as IdCard,
  Mail,
  MapPin,
  Phone,
  User,
  Users,
} from "lucide-react";
import { profilePicQuery } from "./user-record";

interface UserDetailsModalProps {
  isOpen: boolean;
  onClose: () => void;
  user: UserType | null;
}

const getTeamName = (teamId?: string) => {
  if (!teamId) return "Unassigned";
  return "Unknown Team";
};

const getDepartmentColor = (department: string) => {
  const colors: Record<string, string> = {
    Administration: "bg-purple-100 text-purple-800",
    "Information Technology": "bg-blue-100 text-blue-800",
    "Accounts & Finance": "bg-green-100 text-green-800",
    "Human Resources": "bg-orange-100 text-orange-800",
    "Customer Service": "bg-cyan-100 text-cyan-800",
    Design: "bg-pink-100 text-pink-800",
    "Quality Control": "bg-yellow-100 text-yellow-800",
    Operations: "bg-gray-100 text-gray-800",
    Management: "bg-indigo-100 text-indigo-800",
    Production: "bg-red-100 text-red-800",
  };
  return colors[department] || "bg-gray-100 text-gray-800";
};

const getGradingColor = (grading: string) => {
  if (grading.startsWith("A")) return "bg-green-100 text-green-800";
  if (grading.startsWith("B")) return "bg-blue-100 text-blue-800";
  if (grading.startsWith("C")) return "bg-yellow-100 text-yellow-800";
  if (grading.startsWith("D")) return "bg-orange-100 text-orange-800";
  return "bg-red-100 text-red-800";
};

// const getProbationColor = (probation: string, isOnProbation: boolean) => {
//   if (probation === "No Probation") return "bg-green-100 text-green-800";
//   if (!isOnProbation) return "bg-gray-100 text-gray-800";
//   if (probation.includes("1") || probation.includes("2"))
//     return "bg-yellow-100 text-yellow-800";
//   return "bg-orange-100 text-orange-800";
// };

export default function UserDetailsModal({
  isOpen,
  onClose,
  user,
}: UserDetailsModalProps) {
  const { data: religions } = useSkillQuery<EnumResponse[]>({
    queryKey: ["religions"],
    queryFn: getUserReligions,
  });

  const { data: employmentStatus } = useSkillQuery<EnumResponse[]>({
    queryKey: ["employmentStatus"],
    queryFn: getUserEmploymentStatus,
  });

  const { data: profilePic } = useQuery(profilePicQuery(user?.employeeId));

  // const probationInfo = calculateProbationRemaining(
  //   user.dateOfJoining,
  //   user.probationPeriod
  // );

  if (!user) return null;

  let image = null;
  if (profilePic) {
    image = URL.createObjectURL(profilePic);
  }

  const age = calculateAge(user.dateOfBirth);

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="lg:min-w-5xl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <User className="h-5 w-5" />
            User Details - {user.fullName}
          </DialogTitle>
          <DialogDescription>
            Comprehensive information for {user.fullName} (Employee ID:{" "}
            {user.employeeId})
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="max-h-[70vh] pr-4">
          <div className="space-y-6">
            {/* Profile Header */}
            <Card>
              <CardContent className="pt-6">
                <div className="flex items-center gap-6">
                  <Avatar className="h-24 w-24">
                    <AvatarImage
                      src={image || "/placeholder.svg"}
                      alt={user.fullName}
                    />
                    <AvatarFallback className="text-2xl">
                      {user.fullName
                        .split(" ")
                        .map((n) => n[0])
                        .join("")
                        .toUpperCase()}
                    </AvatarFallback>
                  </Avatar>
                  <div className="flex-1 space-y-2">
                    <h2 className="text-2xl font-bold">{user.fullName}</h2>
                    <div className="flex flex-wrap gap-2">
                      <Badge
                        className={`${getDepartmentColor(
                          user.department?.name ?? ""
                        )}`}
                        variant="secondary"
                      >
                        {user.department?.name}
                      </Badge>
                      <Badge variant="outline">{user.role?.name}</Badge>
                      <Badge
                        className={`${getGradingColor(user.grading)}`}
                        variant="secondary"
                      >
                        Grade: {user.grading}
                      </Badge>
                    </div>
                    <div className="flex items-center gap-4 text-sm text-muted-foreground">
                      <div className="flex items-center gap-1">
                        <IdCard className="h-4 w-4" />
                        {user.employeeId}
                      </div>
                      <div className="flex items-center gap-1">
                        <Calendar className="h-4 w-4" />
                        Joined {formatDate(user.dateOfJoining)}
                      </div>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Personal Information */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg flex items-center gap-2">
                    <User className="h-5 w-5" />
                    Personal Information
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Full Name
                      </p>
                      <p className="font-medium">{user.fullName}</p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        National ID
                      </p>
                      <p className="font-mono">{user.nid}</p>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <CakeIcon className="h-4 w-4" />
                        Date of Birth
                      </p>
                      <p>
                        {formatDate(user.dateOfBirth)} ({age} years old)
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Droplets className="h-4 w-4" />
                        Blood Group
                      </p>
                      <Badge variant="outline" className="font-mono">
                        {user.bloodGroup}
                      </Badge>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        EnumResponse
                      </p>
                      <p className="font-medium">
                        {
                          religions?.data.find((r) => r.value === user.religion)
                            ?.name
                        }
                      </p>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Phone className="h-4 w-4" />
                        Mobile Number
                      </p>
                      <p className="font-mono">{user.mobileNumber}</p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Mail className="h-4 w-4" />
                        Email Address
                      </p>
                      <p className="break-all">{user.email}</p>
                    </div>
                  </div>

                  <div>
                    <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                      <MapPin className="h-4 w-4" />
                      Address
                    </p>
                    <p className="text-sm leading-relaxed">{user.address}</p>
                  </div>
                </CardContent>
              </Card>

              {/* Emergency Contact */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg flex items-center gap-2">
                    <AlertTriangle className="h-5 w-5" />
                    Emergency Contact
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div>
                    <p className="text-sm font-medium text-muted-foreground">
                      Contact Person
                    </p>
                    <p className="font-medium">{user.emergencyContactName}</p>
                  </div>
                  <div>
                    <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                      <Phone className="h-4 w-4" />
                      Contact Number
                    </p>
                    <p className="font-mono">{user.emergencyContactNumber}</p>
                  </div>
                </CardContent>
              </Card>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Professional Information */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg flex items-center gap-2">
                    <Briefcase className="h-5 w-5" />
                    Professional Information
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Employee ID
                      </p>
                      <Badge variant="outline" className="font-mono">
                        {user.employeeId}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Calendar className="h-4 w-4" />
                        Date of Joining
                      </p>
                      <p>{formatDate(user.dateOfJoining)}</p>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Building className="h-4 w-4" />
                        Department
                      </p>
                      <Badge
                        className={`${getDepartmentColor(
                          user.department?.name ?? ""
                        )}`}
                        variant="secondary"
                      >
                        {user.department?.name}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Role
                      </p>
                      <Badge variant="outline">{user.role?.name ?? ""}</Badge>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <GraduationCap className="h-4 w-4" />
                        Grading
                      </p>
                      <Badge
                        className={`${getGradingColor(user.grading)}`}
                        variant="secondary"
                      >
                        {user.grading}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Users className="h-4 w-4" />
                        Team Assignment
                      </p>
                      {user.teamId ? (
                        <Badge variant="outline">
                          {getTeamName(user.teamId)}
                        </Badge>
                      ) : (
                        <span className="text-muted-foreground">
                          Unassigned
                        </span>
                      )}
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Employment Status
                      </p>
                      <Badge variant="outline" className="font-medium">
                        {employmentStatus?.data.find(
                          (status) => status.value === user.employmentStatus
                        )?.name ?? ""}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Clock className="h-4 w-4" />
                        Probation Period
                      </p>
                      <Badge variant="outline">{user.probationPeriod}</Badge>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Financial Information */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg flex items-center gap-2">
                    <DollarSign className="h-5 w-5" />
                    Financial Information
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Basic Salary
                      </p>
                      <p className="text-xl font-bold text-green-600">
                        {formatSalary(user.basicSalary)}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Gross Salary
                      </p>
                      <p className="text-lg font-semibold">
                        {formatSalary(
                          user.basicSalary +
                            user.medicalAllowance +
                            user.conveyanceAllowance +
                            user.houseRent
                        )}
                      </p>
                    </div>
                  </div>

                  <Separator />

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        House Rent Allowance
                      </p>
                      <p className="text-lg font-semibold">
                        {formatSalary(user.houseRent)}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Medical Allowance
                      </p>
                      <p className="text-lg font-semibold">
                        {formatSalary(user.medicalAllowance)}
                      </p>
                    </div>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-sm font-medium text-muted-foreground">
                        Conveyance Allowance
                      </p>
                      <p className="text-lg font-semibold">
                        {formatSalary(user.conveyanceAllowance)}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm font-medium text-muted-foreground flex items-center gap-1">
                        <Calculator className="h-4 w-4" />
                        Overtime Amount (per hour)
                      </p>
                      <div>
                        <p className="text-lg font-semibold">
                          {formatSalary(user.overTimeAmount)}
                        </p>
                        <p className="text-xs text-muted-foreground">
                          Formula: (salary ÷ 30 ÷ 8) × 2 × 0.6
                        </p>
                      </div>
                    </div>
                  </div>

                  <Separator />

                  <div>
                    <p className="text-sm font-medium text-muted-foreground">
                      Overhead Cost
                    </p>
                    <p className="text-lg font-semibold">
                      {formatSalary(user.overheadCost)}
                    </p>
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Additional Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Record Information</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="font-medium text-muted-foreground">
                      Created At
                    </p>
                    <p>{formatDate(user.createdAt)}</p>
                  </div>
                  <div>
                    <p className="font-medium text-muted-foreground">
                      Last Updated
                    </p>
                    <p>{formatDate(user.updatedAt)}</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </ScrollArea>
      </DialogContent>
    </Dialog>
  );
}
