"use client";

import type React from "react";

import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import CustomRHFDateInput from "@/components/ui/CustomRHFDateInput";
import CustomRHFInput from "@/components/ui/CustomRHFInput";
import CustomRHFSelect from "@/components/ui/CustomRHFSelect";
import CustomRHFTextArea from "@/components/ui/CustomRHFTextArea";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { ScrollArea } from "@/components/ui/scroll-area";
import { SelectItem } from "@/components/ui/select";
import { useSkillMutation } from "@/hooks/useSkillMutation";
import useSkillQuery from "@/hooks/useSkillQuery";
import { getDepartments } from "@/lib/api/departmentApi";
import { getRoles } from "@/lib/api/roleApi";
import { getTeams } from "@/lib/api/teamApi";
import {
  createUser,
  getUserReligions,
  updateUser,
  uploadUserProfilePicture,
} from "@/lib/api/userApi";
import { toBangladeshTime } from "@/lib/timeHelper";
import {
  defaultUserProfileValues,
  UserProfileForm,
  userProfileSchema,
} from "@/lib/validation/user";
import { EnumResponse } from "@/types/base";
import { Department } from "@/types/department";
import { Role } from "@/types/role";
import { Team } from "@/types/team";
import type { User as UserType } from "@/types/user";
import {
  bloodGroupOptions,
  calculateOvertimeAmount,
  gradingOptions,
  probationPeriodOptions,
} from "@/types/user";
import { zodResolver } from "@hookform/resolvers/zod";
import { useQuery } from "@tanstack/react-query";
import { Save, Upload, User, X } from "lucide-react";
import { useEffect, useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { toast } from "sonner";
import { profilePicQuery } from "./user-record";

interface UserDialogProps {
  isOpen: boolean;
  onClose: () => void;
  user?: UserType | null;
}

export default function UserDialog({ isOpen, onClose, user }: UserDialogProps) {
  // Get teams
  const { data: teams } = useSkillQuery<Team[]>({
    queryKey: ["teams"],
    queryFn: getTeams,
  });
  // Get roles
  const { data: roles } = useSkillQuery<Role[]>({
    queryKey: ["roles"],
    queryFn: getRoles,
  });
  // Get departments
  const { data: departments } = useSkillQuery<Department[]>({
    queryKey: ["departments"],
    queryFn: getDepartments,
  });
  // Get religions
  const { data: religions } = useSkillQuery<EnumResponse[]>({
    queryKey: ["religions"],
    queryFn: getUserReligions,
  });

  // Form
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    watch,
    formState: { errors, isSubmitting },
  } = useForm<UserProfileForm>({
    resolver: zodResolver(userProfileSchema),
  });

  // #region API Calls

  const { mutateAsync: createUserFunc } = useSkillMutation({
    mutationFn: (user: UserProfileForm) => createUser(user),
    queryKey: ["users"],
  });

  const { mutateAsync: uploadProfilePicFunc } = useSkillMutation({
    mutationFn: ({
      employeeId,
      body,
    }: {
      employeeId: string;
      body: FormData;
    }) => uploadUserProfilePicture({ employeeId, body }),
    onClose,
    reset,
    queryKey: ["profilePics"],
  });

  const { mutateAsync: updateUserFunc } = useSkillMutation({
    mutationFn: ({ user, userId }: { user: UserProfileForm; userId: string }) =>
      updateUser(user, userId),
    onClose,
    reset,
    queryKey: ["users"],
  });

  const { data: profilePic } = useQuery(profilePicQuery(user?.employeeId));

  // const [formData, setFormData] = useState(initialFormData);
  const [joiningDate, setJoiningDate] = useState<Date>();
  const [birthDate, setBirthDate] = useState<Date>();
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [imageFile, setImageFile] = useState<File | undefined>(undefined);

  const fileInputRef = useRef<HTMLInputElement>(null);

  const salaries = watch([
    "basicSalary",
    "houseRent",
    "medicalAllowance",
    "conveyanceAllowance",
  ]);

  useEffect(() => {
    setValue(
      "overTimeAmount",
      calculateOvertimeAmount(
        salaries.reduce((a, b) => Number(a) + Number(b), 0)
      )
    );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [salaries]);

  useEffect(() => {
    if (user) {
      reset({
        ...user,
        dateOfBirth: new Date(user.dateOfBirth),
        dateOfJoining: new Date(user.dateOfJoining),
      });
      setBirthDate(toBangladeshTime(user.dateOfBirth));
      setJoiningDate(toBangladeshTime(user.dateOfJoining));
      setValue("dateOfBirth", toBangladeshTime(user.dateOfBirth) || new Date());
      setValue(
        "dateOfJoining",
        toBangladeshTime(user.dateOfJoining) || new Date()
      );
    } else {
      reset(defaultUserProfileValues);
      const employeeId = Math.floor(new Date().getTime() / 1000000);
      setImagePreview(null);
      setValue("employeeId", "EMP" + employeeId);
    }
  }, [user, reset, setValue]);

  const handleDateChange = ({ field, date }: { field: string; date: Date }) => {
    if (field === "dateOfJoining") {
      setJoiningDate(date);
    } else {
      setBirthDate(date);
    }
  };

  const handleImageUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    setImageFile(file);
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const result = e.target?.result as string;
        setImagePreview(result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleClose = () => {
    setJoiningDate(undefined);
    setBirthDate(undefined);
    onClose();
  };

  const onSubmit = async (data: UserProfileForm) => {
    const toastId = toast.loading("Saving...");
    try {
      if (user) {
        const res = await updateUserFunc({ user: data, userId: user.id });

        if (res.success && imageFile) {
          toast.loading("Uploading profile picture...", { id: toastId });

          const formData = new FormData();
          formData.append("File", imageFile);

          await uploadProfilePicFunc({
            employeeId: res.data.id,
            body: formData,
          });
        }
        toast.success("User updated successfully!", { id: toastId });
      } else {
        const res = await createUserFunc(data);

        if (res.success && imageFile) {
          toast.loading("Uploading profile picture...", { id: toastId });

          const formData = new FormData();
          formData.append("File", imageFile);

          await uploadProfilePicFunc({
            employeeId: res.data.id,
            body: formData,
          });
        }
        toast.success("User updated successfully!", { id: toastId });
      }
    } catch (error) {
      console.error(error);
      toast.error("Unexpected error occurred", { id: toastId });
    }
  };

  let image = null;
  if (profilePic && user) image = URL.createObjectURL(profilePic);

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="min-w-[80vw] lg:min-w-5xl">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <User className="h-5 w-5" />
            {user ? "Edit User" : "Add New User"}
          </DialogTitle>
          <DialogDescription>
            {user
              ? "Update user information below."
              : "Enter user details to create a new user account."}
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="max-h-[70vh] pr-4">
          <form className="space-y-6">
            {/* Profile Picture Section */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Profile Picture</CardTitle>
                <CardDescription>
                  Upload a profile picture for the user
                </CardDescription>
              </CardHeader>
              <CardContent className="flex items-center gap-4">
                <Avatar className="h-20 w-20">
                  <AvatarImage
                    src={imagePreview || image || "/placeholder.svg"}
                    alt={user?.fullName || "User"}
                  />
                  <AvatarFallback>
                    {user?.fullName
                      ? user?.fullName
                          .split(" ")
                          .map((n) => n[0])
                          .join("")
                          .toUpperCase()
                      : "U"}
                  </AvatarFallback>
                </Avatar>
                <div className="space-y-2">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={() => fileInputRef.current?.click()}
                    className="flex items-center gap-2"
                  >
                    <Upload className="h-4 w-4" />
                    Upload Picture
                  </Button>
                  <input
                    ref={fileInputRef}
                    type="file"
                    accept="image/*"
                    onChange={handleImageUpload}
                    className="hidden"
                  />
                  <p className="text-xs text-muted-foreground">
                    Recommended: Square image, max 2MB
                  </p>
                </div>
              </CardContent>
            </Card>

            {/* Personal Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Personal Information</CardTitle>
                <CardDescription>Basic personal details</CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                <CustomRHFInput
                  label="Full Name"
                  id="fullName"
                  register={register}
                  errors={errors}
                />

                <CustomRHFInput
                  label="National ID (NID)"
                  id="nid"
                  register={register}
                  errors={errors}
                />

                <CustomRHFInput
                  label="Mobile Number"
                  id="mobileNumber"
                  register={register}
                  errors={errors}
                />

                <CustomRHFInput
                  label="Email Address"
                  id="email"
                  type="email"
                  register={register}
                  errors={errors}
                />

                <CustomRHFSelect
                  label="Blood Group"
                  id="bloodGroup"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.bloodGroup}
                >
                  {bloodGroupOptions.map((bloodGroup) => (
                    <SelectItem key={bloodGroup} value={bloodGroup}>
                      {bloodGroup}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFDateInput
                  label="Date of Birth"
                  id="dateOfBirth"
                  errors={errors}
                  setValue={setValue}
                  handleDateChange={handleDateChange}
                  dateValue={birthDate}
                />

                <CustomRHFSelect
                  label="Religion"
                  id="religion"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.religion?.toString()}
                  valueAsNumber
                >
                  {religions?.data.map((religion) => (
                    <SelectItem
                      key={religion.value}
                      value={religion.value.toString()}
                    >
                      {religion.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <div className="md:col-span-2 lg:col-span-3">
                  <CustomRHFTextArea
                    label="Address"
                    id="address"
                    register={register}
                    errors={errors}
                  />
                </div>
              </CardContent>
            </Card>

            {/* Emergency Contact Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Emergency Contact</CardTitle>
                <CardDescription>
                  Emergency contact person details
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <CustomRHFInput
                  label="Emergency Contact Name"
                  id="emergencyContactName"
                  register={register}
                  errors={errors}
                />

                <CustomRHFInput
                  label="Emergency Contact Number"
                  id="emergencyContactNumber"
                  register={register}
                  errors={errors}
                />
              </CardContent>
            </Card>

            {/* Professional Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">
                  Professional Information
                </CardTitle>
                <CardDescription>
                  Work-related details and assignments
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                <CustomRHFInput
                  label="Employee ID"
                  id="employeeId"
                  register={register}
                  errors={errors}
                  disabled
                  className="bg-muted"
                  helperText="Auto-generated by the system"
                />

                <CustomRHFDateInput
                  label="Date of Joining"
                  id="dateOfJoining"
                  errors={errors}
                  setValue={setValue}
                  handleDateChange={handleDateChange}
                  dateValue={joiningDate}
                />

                <CustomRHFSelect
                  label="Department"
                  id="departmentId"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.departmentId}
                >
                  {departments?.data.map((department) => (
                    <SelectItem key={department.id} value={department.id}>
                      {department.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFSelect
                  label="Role"
                  id="roleId"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.roleId}
                >
                  {roles?.data.map((role) => (
                    <SelectItem key={role.id} value={role.id}>
                      {role.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFSelect
                  label="Grading"
                  id="grading"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.grading}
                >
                  {gradingOptions.map((grading) => (
                    <SelectItem key={grading} value={grading}>
                      {grading}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFSelect
                  label="Probation Period"
                  id="probationPeriod"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.probationPeriod}
                >
                  {probationPeriodOptions.map((probation) => (
                    <SelectItem key={probation} value={probation}>
                      {probation}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFSelect
                  label="Team"
                  id="teamId"
                  setValue={setValue}
                  errors={errors}
                  defaultValue={user?.teamId}
                >
                  {teams?.data.map((team) => (
                    <SelectItem key={team.id} value={team.id}>
                      {team.name}
                    </SelectItem>
                  ))}
                </CustomRHFSelect>

                <CustomRHFInput
                  label="Designation"
                  id="designation"
                  register={register}
                  errors={errors}
                />

                <CustomRHFInput
                  label="Password"
                  id="password"
                  register={register}
                  errors={errors}
                  helperText={
                    user ? "  You cannot change password from here" : ""
                  }
                  disabled={user ? true : false}
                  className={user ? "bg-muted" : ""}
                />
              </CardContent>
            </Card>

            {/* Financial Information */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Financial Information</CardTitle>
                <CardDescription>
                  Salary and cost-related details
                </CardDescription>
              </CardHeader>
              <CardContent className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                <CustomRHFInput
                  label="Basic Salary (BDT)"
                  id="basicSalary"
                  register={register}
                  errors={errors}
                  valueAsNumber
                />

                <CustomRHFInput
                  label="House Allowance (BDT)"
                  id="houseRent"
                  register={register}
                  errors={errors}
                  valueAsNumber
                />

                <CustomRHFInput
                  label="Medical Allowance (BDT)"
                  id="medicalAllowance"
                  register={register}
                  errors={errors}
                  valueAsNumber
                />

                <CustomRHFInput
                  label="Conveyance Allowance (BDT)"
                  id="conveyanceAllowance"
                  register={register}
                  errors={errors}
                  valueAsNumber
                />

                <CustomRHFInput
                  label="Over Time Amount (BDT)"
                  id="overTimeAmount"
                  register={register}
                  errors={errors}
                  valueAsNumber
                  disabled
                  className="bg-muted"
                />

                <div className="space-y-2">
                  <Label
                    htmlFor="grossSalary"
                    className="flex items-center gap-1"
                  >
                    Gross Salary (BDT)
                  </Label>
                  <Input
                    id="grossSalary"
                    type="number"
                    min="0"
                    placeholder="Auto calculated"
                    readOnly
                    disabled
                    className="bg-muted"
                    value={salaries.reduce((a, b) => Number(a) + Number(b), 0)}
                  />

                  <p className="text-xs text-muted-foreground">
                    Auto calculated
                  </p>
                </div>

                <CustomRHFInput
                  label="Overhead Cost (BDT)"
                  id="overheadCost"
                  register={register}
                  errors={errors}
                  valueAsNumber
                />
              </CardContent>
            </Card>
          </form>
        </ScrollArea>

        <DialogFooter>
          <Button type="button" variant="outline" onClick={handleClose}>
            <X className="mr-2 h-4 w-4" />
            Cancel
          </Button>
          <Button
            type="submit"
            onClick={handleSubmit(onSubmit)}
            disabled={isSubmitting}
          >
            <Save className="mr-2 h-4 w-4" />
            {user ? "Update User" : "Create User"}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
