import z from "zod";

export const userProfileSchema = z.object({
  fullName: z.string().min(1, "Full name is required"),
  nid: z.string().min(1, "NID is required"),
  mobileNumber: z.string().min(5, "Mobile number is required"),
  email: z.email("Invalid email"),
  bloodGroup: z.string().min(1, "Blood group is required"),
  dateOfBirth: z.date({ error: "Date of birth is required" }),
  address: z.string().min(1, "Address is required"),
  religion: z.number().min(0, "Religion is required"),

  emergencyContactName: z.string().min(1, "Emergency contact name required"),
  emergencyContactNumber: z
    .string()
    .min(1, "Emergency contact number required"),

  employeeId: z.string().min(1, "Employee ID is required"),
  dateOfJoining: z.date({ error: "Joining date is required" }),
  departmentId: z.string().min(1, "Department is required"),
  roleId: z.string().min(1, "Role is required"),
  grading: z.string().min(1, "Grading is required"),
  probationPeriod: z.string().min(1, "Probation period is required"),
  teamId: z.string().min(1, "Team is required"),
  designation: z.string().min(1, "Designation is required"),

  basicSalary: z.number().nonnegative(),
  houseRent: z.number().nonnegative(),
  medicalAllowance: z.number().nonnegative(),
  conveyanceAllowance: z.number().nonnegative(),
  overTimeAmount: z.number().nonnegative(),
  overheadCost: z.number().nonnegative(),
  password: z.string().min(6, "Password must be at least 6 characters"),
});

export type UserProfileForm = z.infer<typeof userProfileSchema>;

export const defaultUserProfileValues: UserProfileForm = {
  fullName: "",
  nid: "",
  mobileNumber: "",
  email: "",
  bloodGroup: "",
  dateOfBirth: undefined as unknown as Date, // RHF needs something
  address: "",
  religion: 0,

  emergencyContactName: "",
  emergencyContactNumber: "",

  employeeId: "",
  dateOfJoining: undefined as unknown as Date,
  departmentId: "",
  roleId: "",
  grading: "",
  probationPeriod: "",
  teamId: "",
  designation: "",

  basicSalary: 0,
  houseRent: 0,
  medicalAllowance: 0,
  conveyanceAllowance: 0,
  overTimeAmount: 0,
  overheadCost: 0,
  password: "",
};
