import { LoadingDots } from "@/components/loading-page";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { TableCell, TableRow } from "@/components/ui/table";
import { getProfilePicture } from "@/lib/api/userApi";
import {
  calculateDurationSinceJoining,
  formatDate,
  formatSalary,
} from "@/lib/utils";
import { User as UserType } from "@/types/user";
import { useQuery } from "@tanstack/react-query";
import {
  Calendar,
  DollarSign,
  Edit,
  Mail,
  MoreHorizontal,
  Phone,
  Trash2,
} from "lucide-react";

interface UserRecordProps {
  user: UserType;
  onViewDetails: (user: UserType) => void;
  onEdit: (user: UserType) => void;
  onDelete: (userId: string) => void;
}

const getRoleBadgeVariant = (role: string) => {
  if (role === "Admin") return "default";
  if (role.includes("PM")) return "secondary";
  if (role.includes("In-charge")) return "outline";
  return "secondary";
};

const getDepartmentColor = (department: string) => {
  const colors: Record<string, string> = {
    Administration: "bg-purple-100 text-purple-800",
    "Information Technology": "bg-blue-100 text-blue-800",
    "Accounts & Finance": "bg-green-100 text-green-800",
    "Human Resources": "bg-orange-100 text-orange-800",
    "Customer Service": "bg-cyan-100 text-cyan-800",
    Design: "bg-pink-100 text-pink-800",
    "Quality Control": "bg-yellow-100 text-yellow-800",
    Operations: "bg-gray-100 text-gray-800",
    Management: "bg-indigo-100 text-indigo-800",
    Production: "bg-red-100 text-red-800",
  };
  return colors[department] || "bg-gray-100 text-gray-800";
};

const getGradingColor = (grading: string) => {
  if (grading.startsWith("A")) return "bg-green-100 text-green-800";
  if (grading.startsWith("B")) return "bg-blue-100 text-blue-800";
  if (grading.startsWith("C")) return "bg-yellow-100 text-yellow-800";
  if (grading.startsWith("D")) return "bg-orange-100 text-orange-800";
  return "bg-red-100 text-red-800";
};

export const profilePicQuery = (employeeId?: string) => ({
  queryKey: ["profilePics"],
  queryFn: () => getProfilePicture({ employeeId: employeeId! }),
  enabled: !!employeeId,
  refetchOnMount: false,
  refetchOnWindowFocus: false,
});

const UserRecord = ({
  user,
  onViewDetails,
  onEdit,
  onDelete,
}: UserRecordProps) => {
  const daysSinceJoining = calculateDurationSinceJoining(user.dateOfJoining);

  const { data, isFetching } = useQuery(profilePicQuery(user.employeeId));

  if (isFetching)
    return (
      <TableRow>
        <LoadingDots className="p-4" />
      </TableRow>
    );

  let image = null;
  if (data) {
    image = URL.createObjectURL(data);
  }

  return (
    <TableRow
      className="cursor-pointer hover:bg-muted/50"
      onClick={() => onViewDetails(user)}
    >
      <TableCell onClick={(e) => e.stopPropagation()}>
        <Avatar className="h-14 w-14">
          <AvatarImage src={image || "/placeholder.svg"} alt={user.fullName} />
          <AvatarFallback>
            {user.fullName
              .split(" ")
              .map((n) => n[0])
              .join("")
              .toUpperCase()}
          </AvatarFallback>
        </Avatar>
      </TableCell>
      <TableCell>
        <div className="flex flex-col gap-1">
          <span className="font-medium">{user.fullName}</span>
          <div className="flex items-center gap-1 text-xs text-muted-foreground">
            <Mail className="h-3 w-3" />
            {user.email}
          </div>
          <div className="flex items-center gap-1 text-xs text-muted-foreground">
            <Phone className="h-3 w-3" />
            {user.mobileNumber}
          </div>
        </div>
      </TableCell>
      <TableCell>
        <Badge variant="outline" className="font-mono text-xs">
          {user.employeeId}
        </Badge>
      </TableCell>
      <TableCell>
        <div className="flex flex-col gap-1">
          <div className="flex items-center gap-1 text-sm">
            <Calendar className="h-3 w-3 text-muted-foreground" />
            {formatDate(user.dateOfJoining)}
          </div>
          <div className="text-xs text-muted-foreground">
            {daysSinceJoining}
          </div>
        </div>
      </TableCell>
      <TableCell>
        <Badge
          className={`text-xs ${getDepartmentColor(
            user.department?.name ?? ""
          )}`}
          variant="secondary"
        >
          {user.department?.name}
        </Badge>
      </TableCell>
      <TableCell>
        <Badge
          variant={getRoleBadgeVariant(user.role?.name ?? "")}
          className="text-xs"
        >
          {user.role?.name}
        </Badge>
      </TableCell>
      <TableCell>
        <Badge
          className={`text-xs ${getGradingColor(user.grading)}`}
          variant="secondary"
        >
          {user.grading}
        </Badge>
      </TableCell>

      <TableCell>
        <div className="flex items-center gap-1 text-sm font-mono">
          <DollarSign className="h-3 w-3 text-muted-foreground" />
          {formatSalary(user.basicSalary)}
        </div>
      </TableCell>
      <TableCell>
        <div className="text-sm font-mono text-muted-foreground">
          {formatSalary(user.overTimeAmount)}
        </div>
      </TableCell>
      <TableCell>
        {user.teamId ? (
          <Badge variant="outline" className="text-xs">
            {user.team?.name}
          </Badge>
        ) : (
          <span className="text-xs text-muted-foreground">Unassigned</span>
        )}
      </TableCell>
      <TableCell onClick={(e) => e.stopPropagation()}>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
              <MoreHorizontal className="h-4 w-4" />
              <span className="sr-only">Open menu</span>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuItem onClick={() => onEdit(user)}>
              <Edit className="mr-2 h-4 w-4" />
              Edit
            </DropdownMenuItem>
            <DropdownMenuItem
              onClick={() => onDelete(user.id)}
              className="text-destructive"
            >
              <Trash2 className="mr-2 h-4 w-4" />
              Delete
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </TableCell>
    </TableRow>
  );
};

export default UserRecord;
